import React, { useState, useEffect, useMemo, useContext, useCallback } from 'react';
import styled from 'styled-components';
import Popup from '../Popup';
import { StoreContext } from '../../Store'
import { openPopup, closePopup } from '../../Store/types';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Button from '@material-ui/core/Button';
import Title from '../Title/Title';
import moment from 'moment';
import { DateTimePicker } from "@material-ui/pickers";
import Box from "@material-ui/core/Box";
import { Typography } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
    dateInput: {
        marginRight: theme.spacing(3)
    }
}));

const ClearButton = styled(Button)`
    margin-left: 16px;
`

export default function Sales() {
    const [, dispatch] = useContext(StoreContext);
    const [sales, setSales] = useState([]);
    const [isAllowed, setIsAllowed] = useState(true);
    const [selectedDate, handleDateChange] = useState(moment());
    const classes = useStyles();

    const calculateSalesCheck = useCallback(() => {
        const calculatedSalesCheck = {sum2h: 0, sum30h: 0};
        sales.forEach(sale => {
            const diff = selectedDate.diff(sale.date, 'm');
            if (diff < 120) calculatedSalesCheck.sum2h++;
            if (diff < 30 * 60) calculatedSalesCheck.sum30h++;
        });
        return calculatedSalesCheck;
    }, [sales, selectedDate]);

    const calculateIsAllowed = useCallback(() => {
        const check = calculateSalesCheck();
        return !(check.sum2h >= 2 || check.sum30h >= 7);
    }, [calculateSalesCheck]);

    /* On Mount. */
    useEffect(() => {
        localStorage.carSell && setSales(JSON.parse(localStorage.carSell));
    }, []); // eslint-disable-line

    useEffect(() => {
        localStorage.setItem('carSell', JSON.stringify(sales));
        setIsAllowed(calculateIsAllowed());
    }, [sales, calculateIsAllowed]);

    const addNewSale = () => {
        const updatedSales = [
            ...sales,
            {
                formattedDate: selectedDate.format('hh:mm:ss DD.MM'),
                id: new Date().getUTCMilliseconds(),
                date: selectedDate
            }
        ].sort(((a, b) => b.date - a.date));
        setSales(updatedSales)
    }

    const dispatchWarningPopup = () => {
        const salesCheck = calculateSalesCheck();
        return dispatch({
            type: openPopup,
            payload: {
                popupText: `Are you sure you wanna sell car?
                    You has sold ${salesCheck.sum2h} cars in last two hours
                    and ${salesCheck.sum30h} cars in last 30 hours so you should do
                    it only if you sure u have first sell level`,
                popupTitle: 'Are you sure you wanna sell car while you have cd?'
            }
        })
    }

    const dispatchSalesClearPopup = () => {
        dispatch({
            type: openPopup,
            payload: {
                popupTitle: 'Clear sell history',
                popupText: 'Are you sure you want delete your history'
            }
        })
    }

    const handleClick = (e) => {
        e.preventDefault();
        const updatedIsAllowed = calculateIsAllowed();
        setIsAllowed(updatedIsAllowed);
        if (!updatedIsAllowed) return dispatchWarningPopup();
        addNewSale();
    }

    const handleClearClick = (e) => {
        e.preventDefault();
        dispatchSalesClearPopup();
        setIsAllowed(true);
    }

    const Sales = useMemo(() => (
        <TableBody>
            {sales.map(({formattedDate, id}) => (
                <TableRow key={id}>
                    <TableCell>{formattedDate}</TableCell>
                </TableRow>
            ))}
        </TableBody>
    ), [sales])

    const handleClearAccept = useCallback(() => {
        setSales([])
        dispatch({type: closePopup})
    }, [dispatch])

    return (
        <>
            <Popup handleAccept={handleClearAccept}/>
            <Title>Recent Sales.</Title>
            <Typography>
                {isAllowed ? 'You can sell now.' : 'You cannot sell now.'}
            </Typography>
            <Box>
                <DateTimePicker className={classes.dateInput} value={selectedDate} onChange={handleDateChange}/>
                <Button onClick={handleClick} variant="contained" color="primary">Sell</Button>
                <ClearButton onClick={handleClearClick} variant="contained" color="secondary">Clear</ClearButton>
            </Box>
            <Table size="small">
                <TableHead>
                    <TableRow>
                        <TableCell>Date</TableCell>
                    </TableRow>
                </TableHead>
                {Sales}
            </Table>
        </>
    );
}
