import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './components';
import Store from './Store';
import * as serviceWorker from './serviceWorker';

ReactDOM.render(
  <React.StrictMode>
    <Store>
      <App />
    </Store>
  </React.StrictMode>,
  document.getElementById('root')
);

serviceWorker.unregister();
