import React, { useContext, useMemo, useCallback } from 'react';
import styled from 'styled-components';
import { StoreContext } from '../../Store'
import { closePopup } from '../../Store/types';
import Button from '@material-ui/core/Button';

const Popup = ({handleAccept = () => false}) => {
    const [state, dispatch] = useContext(StoreContext);
    const {isOpen, popupTitle, popupText} = state;

    const handleDecline = useCallback(() => {
        dispatch({type: closePopup})
    }, [dispatch])

    return useMemo(() =>
        <DialogWrapper isOpen={isOpen}>
            <Dialog>
                <Title>{popupTitle}</Title>
                <Text>{popupText}</Text>
                <ButtonWrapper>
                    <AcceptButton onClick={handleAccept} variant="contained" color="primary">Yes</AcceptButton>
                    <DeclineButton onClick={handleDecline} variant="contained" color="secondary">No</DeclineButton>
                </ButtonWrapper>
            </Dialog>
        </DialogWrapper>, [isOpen, popupTitle, popupText, handleAccept, handleDecline]
    )
}

export default Popup;

const DialogWrapper = styled('div')`
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,.5);
  position: absolute;
  overflow: hidden;
  display: ${props => props.isOpen ? 'block' : 'none'};
`

const Dialog = styled('div')`
  width: 500px;
  border: 1px solid black;
  background-color: #eee;
  position: fixed;
  display: block;
  top: 20vh;
  left: 35vw;
  border-radius: 6px;
`

const Title = styled('div')`
  color: red;
  width: 100%;
  font-size: 22px;
  font-family: 'Roboto';
  border-bottom: 1px solid #eee;
  padding: 8px 12px;
  margin-bottom: 20px;
`

const Text = styled(Title)`
  color: black;
  font-size: 16px;
  padding: 50px 12px;
  text-align: center;
`

const ButtonWrapper = styled('div')`
  text-align: end;
  padding: 15px;
`

const AcceptButton = styled(Button)`
  margin-right: 15px;
  width: 100px;
`

const DeclineButton = styled(AcceptButton)``
