import {
  openPopup,
  closePopup,
  setError,
  updateAnyState
} from './types';

export default (state, { type, payload }) => {
  const dictionary = {
    [openPopup]: () => ({ ...state, isOpen: true, popupTitle: payload.popupTitle, popupText: payload.popupText }),
    [closePopup]: () => ({ ...state, isOpen: false, popupTitle: '', popupText: '' }),
    [setError]: () => ({ ...state, error: payload }),
    [updateAnyState]: () => ({ ...state, ...payload }),
    default: () => state
  }

  return dictionary[type] ? dictionary[type]() : dictionary.default();
};
